/* Calendar container */
.calendar-view {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #f5f5f5; /* Light gray background */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 900px;
    margin: 0 auto;
    overflow: hidden; /* Ensures elements inside don’t overflow the container */
  }
  
  /* Header styling */
  .header {
    text-align: center;
    margin-bottom: 30px;
    background-color: #4CAF50; /* Clean, vibrant green background */
    padding: 15px;
    border-radius: 8px;
  }
  
  h2 {
    font-size: 2.5em;
    color: #fff; /* White text for header */
    font-weight: bold;
    margin: 0;
  }
  
  /* Styling for react-calendar */
  .react-calendar {
    border: none;
    border-radius: 8px;
    background-color: #ffffff; /* White background */
    margin: 0 auto;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 10px;
    margin-top: 20px;
  }
  
  /* Styling for month navigation */
  .react-calendar__navigation {
    margin-bottom: 10px; /* Adds space below the navigation */
    display: flex;
    justify-content: center; /* Centers the navigation buttons */
    font-size: 1.2em;
  }
  
  .react-calendar__navigation button {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    margin: 0 5px;
    cursor: pointer;
    font-weight: bold;
  }
  
  .react-calendar__navigation button:hover {
    background-color: #45a049; /* Slightly darker green on hover */
  }
  
  .react-calendar__navigation button[disabled] {
    background-color: #e0e0e0; /* Disabled state for buttons */
    cursor: not-allowed;
  }
  
  /* Calendar tiles styling */
  .react-calendar__tile {
    border-radius: 8px;
    font-size: 1.1em;
    padding: 10px;
    color: #333; /* Dark text color for readability */
    background-color: #f8f8f8; /* Light gray background for tiles */
  }
  
  .react-calendar__tile--now {
    background-color: #4CAF50; /* Vibrant green for today's date */
    color: white;
    font-weight: bold;
  }
  
  .react-calendar__tile--active {
    background-color: #64b5f6; /* Soft blue for active selected date */
    color: white;
  }
  
  .react-calendar__tile:hover {
    background-color: #e0e0e0; /* Light gray hover effect */
  }
  
  .react-calendar__month-view__days__day {
    color: #333;
    font-weight: 600;
    font-size: 1.2em;
  }
  
  /* Information section below the calendar */
  .communications-info {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 30px;
  }
  
  .date-info h3 {
    font-size: 1.8em;
    margin-bottom: 20px;
    color: #4CAF50; /* Green text color for the date */
    text-align: center;
    background-color: #e8f5e9; /* Light green background */
    padding: 10px;
    border-radius: 8px;
  }
  
  .communications-section {
    margin-bottom: 20px;
  }
  
  .communications-section h4 {
    font-size: 1.3em;
    margin-bottom: 10px;
    text-decoration: underline;
    color: #333;
  }
  
  .past {
    background-color: #f8d7da;
    padding: 10px;
    border-radius: 5px;
  }
  
  .upcoming {
    background-color: #fff3cd;
    padding: 10px;
    border-radius: 5px;
  }
  
  ul {
    list-style: none;
    padding-left: 0;
  }
  
  li {
    font-size: 1.1em;
    margin-bottom: 5px;
  }
  
  .company {
    font-weight: bold;
  }
  
  .type {
    color: #555;
  }
  
  .no-communications {
    background-color: #e0e0e0;
    padding: 15px;
    border-radius: 5px;
    text-align: center;
    font-size: 1.2em;
    color: #333;
    font-weight: bold;
    margin-top: 20px;
  }
  
  @media (max-width: 768px) {
    .calendar-view {
      padding: 10px;
    }
  
    .react-calendar {
      width: 100%;
    }
  
    .communications-info {
      padding: 15px;
    }
  }
  