.app-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #f4f4f4;
  }
  
  .main-content {
    padding: 20px;
    flex: 1;
  }
  
  .welcome-message {
    text-align: center;
    color: #35424a;
    font-size: 2rem;
    font-weight: bold;
    margin-top: 50px;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
  }
  
  .header {
    background-color: #35424a;
    color: #ffffff;
    padding: 10px;
    text-align: center;
  }
  
  .footer {
    background-color: #35424a;
    color: #ffffff;
    text-align: center;
    padding: 10px 0;
    position: relative;
    bottom: 0;
    width: 100%;
  }
  
  /* Button styles */
  .button {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #0056b3;
  }
  
  /* Tooltip Styles */
  .tooltip {
    position: relative;
    display: inline-block;
  }
  
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }
  
  /* Highlight styles */
  .highlight-red {
    background-color: #ffcccc;
  }
  
  .highlight-yellow {
    background-color: #ffffcc;
  }
  
  /* Notification Styles */
  .notification {
    padding: 10px;
    border-radius: 5px;
  }
  
  .notification.overdue {
    background-color: #f8d7da;
    color: #721c24;
  }
  
  .notification.due-today {
    background-color: #fff3cd;
    color: #856404;
  }