/* General Container */
.dashboard-container {
    max-width: 80%;
    margin: auto;
    padding: 20px;
    background: linear-gradient(to bottom right, #f5f7fa, #c3cfe2);
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  /* Title */
  .dashboard-title {
    text-align: center;
    font-size: 2.4rem;
    font-weight: bold;
    color: #34495e;
    margin-bottom: 20px;
    text-transform: uppercase;
    position: relative;
  }
  
  .dashboard-title::after {
    content: "";
    display: block;
    width: 50px;
    height: 4px;
    background: #2980b9;
    margin: 10px auto;
  }
  
  /* Table */
  .dashboard-table {
    width: 100%;
    border-collapse: collapse;
    background: #fff;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .dashboard-table th,
  .dashboard-table td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .dashboard-table th {
    background: #34495e;
    color: #fff;
    text-transform: uppercase;
  }
  
  .dashboard-table tr:hover {
    background: #f1f1f1;
  }
  
  /* Row Styling */
  .row-overdue {
    background: #ffe0e0;
  }
  
  .row-due-today {
    background: #e0ffe0;
  }
  
  /* Buttons */
  .button-action {
    padding: 8px 16px;
    background: #3498db;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .button-action:hover {
    background: #2980b9;
  }
  
  /* Modal */
  .modal {
    width: 50%;
    margin: auto;
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  }
  
  .modal-overlay {
    background: rgba(0, 0, 0, 0.5);
  }
  
  .modal-title {
    font-size: 1.8rem;
    color: #34495e;
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 16px;
  }
  
  .form-select,
  .form-input,
  .form-textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .modal-actions {
    display: flex;
    justify-content: space-between;
  }
  
  .button-submit {
    padding: 10px 20px;
    background: #27ae60;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .button-cancel {
    padding: 10px 20px;
    background: #e74c3c;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .button-submit:hover {
    background: #2ecc71;
  }
  
  .button-cancel:hover {
    background: #c0392b;
  }
  