/* Container and Background */
.communication-management {
    max-width: 700px;
    margin: 40px auto;
    padding: 30px;
    font-family: 'Roboto', sans-serif;
    border-radius: 16px;
    background: linear-gradient(135deg, #e0f7fa, #ffffff);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    transition: background 0.5s ease;
  }
  
  .communication-management:hover {
    background: linear-gradient(135deg, #c8e6c9, #ffffff);
  }
  
  /* Heading Styling */
  .title {
    text-align: center;
    margin-bottom: 25px;
    font-size: 28px;
    font-weight: 800;
    color: #2c3e50;
    text-transform: uppercase;
    letter-spacing: 1px;
    position: relative;
  }
  
  .title::after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 4px;
    background: #00796b;
    border-radius: 2px;
  }
  
  /* Form Fields */
  .form-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 30px;
  }
  
  .input-field {
    padding: 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 12px;
    transition: all 0.3s ease;
  }
  
  .input-field:focus {
    border-color: #00796b;
    box-shadow: 0 0 8px rgba(0, 121, 107, 0.4);
    outline: none;
  }
  
  /* Checkbox Styling */
  .checkbox-container {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 16px;
    color: #2c3e50;
  }
  
  .checkbox-container input[type="checkbox"] {
    appearance: none;
    width: 20px;
    height: 20px;
    background: #fff;
    border: 2px solid #00796b;
    border-radius: 4px;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .checkbox-container input[type="checkbox"]:checked {
    background-color: #00796b;
    box-shadow: 0 0 5px rgba(0, 121, 107, 0.5);
    position: relative;
  }
  
  .checkbox-container input[type="checkbox"]:checked::after {
    content: '✓';
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 3px;
    left: 5px;
  }
  
  /* Button Styling */
  .add-button {
    padding: 14px;
    background: linear-gradient(135deg, #00796b, #004d40);
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    border: none;
    border-radius: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .add-button:hover {
    transform: translateY(-2px);
    background: linear-gradient(135deg, #004d40, #00796b);
    box-shadow: 0 6px 12px rgba(0, 121, 107, 0.4);
  }
  
  /* List Styling */
  .method-list {
    list-style: none;
    padding: 0;
  }
  
  .method-item {
    padding: 18px;
    margin-bottom: 15px;
    border-radius: 16px;
    background: #ffffff;
    border: 1px solid #e0e0e0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .method-item:hover {
    transform: translateY(-4px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  
  /* Background Effect */
  body {
    background: linear-gradient(45deg, #e3f2fd, #e0f7fa, #f3e5f5);
    animation: bgAnimation 6s infinite alternate;
  }
  
  @keyframes bgAnimation {
    0% {
      background-position: 0% 50%;
    }
    100% {
      background-position: 100% 50%;
    }
  }
  