.communication-action-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    border-radius: 12px;
    background: linear-gradient(145deg, #ffffff, #e6e6e6);
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
  }
  
  .title {
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
  }
  
  .form-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .input-field, .textarea-field {
    width: 100%;
    padding: 10px;
    border-radius: 8px;
    border: 1px solid #ccc;
    background: #f9f9f9;
    font-size: 1rem;
    transition: all 0.3s ease;
  }
  
  .input-field:focus, .textarea-field:focus {
    outline: none;
    border-color: #007bff;
    background: #ffffff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
  
  .textarea-field {
    resize: none;
    height: 100px;
  }
  
  .log-button {
    padding: 10px 15px;
    border: none;
    border-radius: 8px;
    background: #007bff;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .log-button:hover {
    background: #0056b3;
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  