/* General Styles */
.notifications {
    max-width: 1000px;
    margin: 30px auto;
    background-color: #f1f5f9;
    padding: 40px;
    border-radius: 15px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

/* Header Styling */
.notification-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 2px solid #ccc;
    margin-bottom: 25px;
}

.notification-header h2 {
    font-size: 28px;
    font-weight: bold;
    color: #2c3e50;
    padding: 12px 20px;
    background-color: #3498db;
    border-radius: 12px;
    color: white;
    display: inline-block;
}

.notification-icon {
    position: relative;
}

.notification-badge {
    position: absolute;
    top: -5px;
    right: -10px;
    background-color: #e74c3c;
    color: white;
    border-radius: 50%;
    padding: 3px 8px;
    font-size: 12px;
    font-weight: bold;
    border: 2px solid #fff; /* Reduced the border size */
}

/* Communications Section Styles */
.communications-section {
    margin-bottom: 35px;
}

h3 {
    font-size: 22px;
    color: #2c3e50;
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 16px;
    background-color: #ecf0f1;
    border-radius: 10px;
}

.badge {
    font-weight: bold;
    padding: 5px 15px;
    border-radius: 20px;
    font-size: 14px;
}

.badge-danger {
    background-color: #e74c3c;
    color: white;
}

.badge-warning {
    background-color: #f39c12;
    color: white;
}

.communications-table {
    width: 100%;
    border-collapse: collapse;
    background-color: white;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.communications-table thead {
    background-color: #3498db;
    color: white;
}

.communications-table th, .communications-table td {
    padding: 15px;
    text-align: left;
    color: #2c3e50;
}

.communications-table th {
    font-size: 16px;
    font-weight: bold;
}

.communications-table td {
    font-size: 14px;
    color: #7f8c8d;
}

/* Highlight Communication Types */
.type-cell {
    display: flex;
    align-items: center;
}

.type-cell svg {
    margin-right: 10px;
}

/* Hover Effect on Table Rows */
.communications-table tbody tr {
    transition: background-color 0.3s ease;
}

.communications-table tbody tr:hover {
    background-color: #ecf0f1;
}

/* Responsive Design */
@media (max-width: 768px) {
    .notifications {
        padding: 20px;
    }

    .notification-header {
        flex-direction: column;
        align-items: flex-start;
    }

    .communications-table th, .communications-table td {
        padding: 10px;
    }

    .notification-badge {
        top: -5px;
        right: -5px;
    }
}
